/* eslint-disable */

/* 3. 컴포넌트 import할 때
- app.js 방문 시 Detail, Cart 다 미리 로드하면 부담
-> lazy loading 하기 */

import React, { useState, useContext, lazy, Suspense } from "react";
import "./App.css";
import { Navbar, Container, Nav, NavDropdown, Button } from "react-bootstrap";
import Data from "./data.js";
// import Detail from "./Detail.js";
let Detail = lazy(() => import("./Detail.js")); // <Detail>을 보여줄 때만 import Detail.js 해옴

import axios from "axios";
import { Link, Route, Switch } from "react-router-dom";

import Cart from "./Cart.js";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";

export let 재고context = React.createContext();

function App() {
  let [shoes, shoesChange] = useState(Data);
  let [재고, 재고변경] = useState([10, 11, 12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              {/* Link 태그 에러 해결하기 */}
              <Nav.Link as={Link} to="/">
                Home
              </Nav.Link>
              <Nav.Link as={Link} to="/detail">
                Detail
              </Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">
                  Another action
                </NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">
                  Something
                </NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">
                  Separated link
                </NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Switch>
        <Route exact path="/">
          <div className="background">
            <h1>20% Season off</h1>
            <p>
              Lorem ipsum, dolor sit amet consectetur adipisicing elit. Dolorem
              in similique mollitia, iusto, itaque quas aperiam praesentium
              laboriosam doloribus perspiciatis temporibus voluptatem maxime,
              voluptate ratione. Eum, quam. Reprehenderit, laborum voluptate?
            </p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </div>
          <div className="container">
            <재고context.Provider value={재고}>
              <div className="row">
                {shoes.map((a, i) => {
                  return <Card shoes={a} i={i} key={i} />;
                })}
              </div>
            </재고context.Provider>

            <button
              className="btn btn-primary"
              onClick={() => {
                axios
                  .get("https://codingapple1.github.io/shop/data2.json")
                  .then((result) => {
                    console.log(result.data);
                    shoesChange([...shoes, ...result.data]);
                  })
                  .catch(() => {
                    console.log("실패했어요");
                  });
              }}
            >
              더보기
            </button>
          </div>
        </Route>

        <Route path="/detail/:id">
          <재고context.Provider value={재고}>
            <Suspense fallback={<div>로딩중이에요</div>}>
              {/* fallback={<div>로딩중이에요</div>} : 로딩 전까지 보여줄 HTML */}
              <Detail shoes={shoes} 재고={재고} 재고변경={재고변경} />
            </Suspense>
          </재고context.Provider>
        </Route>

        <Route path="/cart">
          <Cart></Cart>
        </Route>
      </Switch>
    </div>
  );
}

function Card(props) {
  let 재고 = useContext(재고context);
  let history = useHistory();

  return (
    /* 메인페이지 상품을 누르면 각각 상세페이지로 이동
    - <Card>에 onClick 달지말고 Card 컴포넌트 안에다가 달아야함
    - 페이지 이동은 history.push() */
    <div
      className="col-md-4"
      onClick={() => {
        history.push("/detail/" + props.shoes.id);
      }}
    >
      <img
        src={
          "https://codingapple1.github.io/shop/shoes" + (props.i + 1) + ".jpg"
        }
        alt=""
        width="100%"
      />
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{props.shoes.price}</p>

      {재고[props.i]}
    </div>
  );
}

export default App;
