import React, { useEffect, memo } from "react";
import { Table } from "react-bootstrap";
import { connect, useDispatch, useSelector } from "react-redux";
import "./Detail.scss";

function Cart(props) {
  function 버튼누르면() {}

  let style = { color: "white" };

  let state = useSelector((state) => state);
  console.log(state);
  console.log(state.reducer);

  let dispatch = useDispatch();

  return (
    <div>
      <Table responsive>
        <thead>
          <tr style={{ color: "white" }}>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경</th>
          </tr>
        </thead>
        <tbody>
          {state.reducer.map((a, i) => {
            return (
              <tr key={i}>
                <td>{a.id}</td>
                <td>{a.name}</td>
                <td>{a.quan}</td>
                <td>
                  <button
                    onClick={() => {
                      dispatch({ type: "수량증가", 데이터: a.id });
                    }}
                  >
                    +
                  </button>
                  <button
                    onClick={() => {
                      dispatch({ type: "수량감소", 데이터: a.id });
                    }}
                  >
                    -
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>

      {state.reducer2 === true ? (
        <div className="my-alert2">
          <p>지금 구매하시면 신규할인 20%</p>
          <button onClick={버튼누르면}>닫기</button>
        </div>
      ) : null}

      <Parent 이름="존박1" 나이="20"/>
    </div>
  );
}
/* 컴포넌트에 있는 props나 state가 변경되면 그거 쓰는 HTML 전부 재렌더링
-> 이름이 존박에서 존박1로 바뀌면 child1, child2 둘 다 재렌더링
-> memo()를 사용하면 이름을 안 쓰는 child2의 불필요한 재렌더링 방지 가능 */
function Parent(props){
  return (
    <div>
      <Child1 이름={props.이름}/>
      <Child2 나이={props.나이}/> 
    </div>
  )
}


function Child1(){
  useEffect( ()=>{ console.log('렌더링됨1') } );// 로딩/ 재렌더링 시 실행할 코드
  return <div>1111</div>
}

/* memo()를 사용하면 불필요한 재렌더링 막기 가능
1. 'react'에서 import { memo }
2. memo()로 컴포넌트 감싸기
3. -> 컴포넌트와 관련된 props가 변경이 될 때만 재렌더링 됨
- 단점: 기존 props vs 바뀐 props 비교연산 후 컴포넌트 업데이트 할지말지 결정함
        (-> props 양이 많으면 느려질 수 있음) */
let Child2 = memo(function () {
  useEffect(() => { console.log('렌더링됨2') });// 로딩/ 재렌더링 시 실행할 코드
  return <div>2222</div>
});






// state 꺼내쓰는 더 쉬운 방법 useSelector()

// function state를props화(state) {
//   return {
//     state: state.reducer,
//     alert열렸니: state.reducer2,
//   };
// }

// export default connect(state를props화)(Cart);

export default Cart;
